.container{
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: row;
}

h1{
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: #303F9F;
    margin: 10px;
    padding: 5px;
    justify-content: center;
}

.titulo{
    width: 50%;
    height: 100px;
    text-align: start;
}


.menu{
    width: 50%;
    height: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.menu-NavBar{
    width: 15px;
    display: flex;
    text-align: center;
    justify-content: center;
}